#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}

.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
  transition: filter 300ms;
}
.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}
.logo.react:hover {
  filter: drop-shadow(0 0 2em #61dafbaa);
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  a:nth-of-type(2) .logo {
    animation: logo-spin infinite 20s linear;
  }
}

.card {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}

/* スマホ用（画面幅600px以下） */
@media (max-width: 600px) {
  body {
    font-size: 15px;
    padding: 0;
  }
  .header {
    flex-direction: column;
    font-size: 1em;
    min-height: 36px;
    padding: 6px 0;
  }
  .header input[type="text"] {
    width: 100% !important;
    margin: 8px 0 0 0 !important;
  }
  .header button, .header label, .header .csv-export-btn {
    font-size: 1em;
    padding: 6px 8px;
    margin: 4px 2px;
  }
  .header-toggle {
    left: 4px;
    top: 4px;
    font-size: 1em;
    padding: 0.2em 0.7em;
  }
  #root {
    max-width: 100vw;
    padding: 0;
  }
  /* サイドバーを画面上に重ねて表示（オーバーレイ風） */
  .sidebar {
    position: fixed !important;
    left: 0; top: 0; bottom: 0;
    width: 80vw !important;
    max-width: 320px;
    z-index: 5000;
    height: 100vh;
    box-shadow: 2px 0 12px rgba(0,0,0,0.18);
    transition: left 0.2s;
  }
  /* AIチャットボタンを画面下部中央に大きく */
  .ai-chat-fab {
    position: fixed !important;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);
    width: 64px;
    height: 64px;
    font-size: 2.2em;
    border-radius: 50%;
    z-index: 4000;
  }
  /* AIチャットウィンドウを全画面に近い表示に */
  .ai-chat-modal {
    position: fixed !important;
    left: 0; right: 0; bottom: 0; top: 0;
    width: 100vw !important;
    height: 100vh !important;
    max-width: 100vw;
    max-height: 100vh;
    border-radius: 0 !important;
    margin: 0;
    z-index: 4100;
    box-shadow: none;
  }
  .ai-chat-modal > div {
    height: 100%;
  }
  /* テーブルやリストの横スクロール */
  table, .product-table {
    display: block;
    overflow-x: auto;
    width: 100%;
  }
}
